              <th>Shortage Cost</th>
            </tr>
          </thead>
          <tbody>
            <tr>
               <td>{{ selectedGrn.receiver }}</td>
              <td>{{ selectedGrn.name + ' - '+ selectedGrn.description }}</td>
              <td>{{ selectedGrn.briefing }}</td>
               <td>{{ selectedGrn.units }}</td>
