<script setup>
    import '@/assets/sass/widgets/widgets.scss';
    import { onMounted,ref,inject,computed } from 'vue';
    import { useMeta } from '@/composables/use-meta';
    import { VFNumber } from 'vue-animation-counter';
    import '@/assets/sass/scrollspyNav.scss';
    import '@/assets/sass/components/custom-counter.scss';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    import { tableOptions } from '@/components/reusables/tableOptions';
    useMeta({ title: 'Dashboard' });
    import axios from 'axios';
    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });
   const table_option = computed(() => tableOptions.value);

const columns = ref(['SNo','name', 'description','units','receiver','received','shortage','briefing','cost','date','actions']);
    

const remark = ref('')
const status_id = ref('')
const sending = ref(false)
const updateGrn = async (id)=>{
  sending.value = true
  const payload = {
    status_id : status_id.value,
    remark : remark.value
  }
  try{
    const response = await axiosInstance.put(`grn-items/${id}`,payload)
    showMessage('Saved!')
    remark.value = ''
    document.getElementById('close2').click()
    getShortageGrn()
  }catch(error){
    console.log(error)
    showError(error.message)
  }finally{
    sending.value = false
  }
}
const loading = ref(false)

     const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
     const summary = ref('')
     const selectedGrn = ref('')
     const showMessage = inject('showMessage');
     const showError = inject('showError');  
     const formatNumber = inject('formatNumber') ;
     const formatDate = inject('formatDate') ;
     const tableShimmer = inject('tableShimmer') ;
     const loadingSpinner = inject('loadingSpinner') ;
     const cardShimmer = inject('cardShimmer')
     const rejection = ref(false)
     const clearForm = () => {
      lpos.value = [];
      spinner.value = null;
      jobcards.value = []
      costNote.value = 'nothing'
     }


    const fetchSummary = () => {
      axiosInstance.get('dashboard/summary')
        .then(response => {
          summary.value = response.data
        })
        .catch(error => {
          showError(error.message);
          console.error('Error fetching summary :', error);
        });
    };
    fetchSummary();
    // setInterval(fetchSummary, 10000);
 
        const lpos = ref([])
   

        const jobcards = ref([])
   
const costNote = ref('nothing')


    


    const spinner = ref('');
const cards = ref([])
function getCountValue(countSource) {
  if (!countSource || typeof countSource !== 'string') return 0;
  try {
    return Function('summary', `return ${countSource}`)(summary.value);
  } catch (e) {
    return 0;
  }
}

const getCards = async ()=>{
  loading.value = true
  try{
    const response = await axiosInstance.get('main-cards')
    cards.value = response.data
  }catch(error){
   console.log(error)
   showError(error.message)
  }finally{
    loading.value = false
  }
}





const shortageGrn = ref([])
const getShortageGrn = async ()=>{
  try{
    const response = await axiosInstance.get('grn-items?status_id=2')
    shortageGrn.value = response.data.map((item,index)=>({
       ...item,
                    SNo: index + 1,
                    id: item.id,
                    date : formatDate(item.created_at),
                    receiver : item?.receiver?.name,
                    grn_no: item.grn_id,
                    name: item.approval_item.requisition_item.remark,
                    description: item.approval_item.requisition_item.description,
                    units: item.approval_item.requisition_item.units,
                    rate: item.approval_item.requisition_item.rate,
                    received: item.quantity,
                    approved: item.approval_item.quantity,
                    shortage: (item.approval_item.quantity - item.quantity).toFixed(2),
                    briefing : item.description,
                    cost: formatNumber(item.approval_item.requisition_item.rate * (item.approval_item.quantity - item.quantity)),
                    rate: item.approval_item.requisition_item.rate,
      
    }))
    if(shortageGrn.value.length > 0 && user_permissions.includes('can_view_grn_shortage_alert') && !JSON.parse(localStorage.getItem("popped"))){
      openModal('shortage-grn')
       localStorage.setItem('popped', true);
    }
    console.log(JSON.parse(localStorage.getItem("popped")));
  }catch(error){
   console.log(error)
   showError(error.message)
  }finally{
   
  }
}

const openModal = (modalName) => {
    const modalEl = document.getElementById(modalName);
    if (!modalEl) return;
    const modal = new bootstrap.Modal(modalEl);
    modal.show();
};

const closeModal = (modalName) => {
    const modalEl = document.getElementById(modalName);
    if (!modalEl) return;
    const modal = new bootstrap.Modal(modalEl);
    modal.hide();
};



const summaries = ref([])
const getSummaries = async () => {
  try {
    const response = await axiosInstance.get('dashboard/summary')
    const data = response.data

    const flattenWithDefaults = (obj, prefix = '') => {
      return Object.keys(obj).flatMap(key => {
        const fullKey = prefix ? `${prefix}.${key}` : key
        if (typeof obj[key] === 'object' && obj[key] !== null) {
          return flattenWithDefaults(obj[key], fullKey)
        } else {
          return `summary?.${fullKey} ?? 0`
        }
      })
    }

    const keysList = flattenWithDefaults(data)
    summaries.value = keysList.map(k => ({ name: k }))
  } catch (error) {
    console.log(error)
    showError(error.message)
  }
}


       onMounted(() => {
        getShortageGrn()
        getCards()
        getSummaries()
    });


    
</script>

